body{
  background:	#000e40;
  text-align:center;
  color:#ffce00;
}

.gameboard{
  margin:0px auto;
  width:75%;
}
.nav-bar h1{
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size:2.5em;
}
.nav-bar p{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size:2em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
