.card-field {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
height: 20vw;
width: 15vw;
margin:5px;
background:black;
border: inset #b89500;
border-radius:2%;
box-shadow: 5px 5px 10px black;
color:white;
}

.card h2{
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin:5px;
}

.image {
  background:black;
  height:90%;
  border: inset #b89500;
  
}


.dummies{
  max-height:90%; max-width:100%;
}